import React, { Component } from 'react';

class BasicScissorsPaperStoneGame extends Component {
  constructor(){
    super();
    this.state = {
      score: 0,
      wrongScore: 0,
      enemyCard: '',
      isRecord: false,
    }
  }

  randomAnswer() {
    return Math.floor(Math.random() * 3) + 1;
  }

  render() {
    return (
      <div className="gameCanvas">
        Lose:
        {' '}
        {this.state.wrongScore}
        <br />
        Won:
        {' '}
        {this.state.score}
        <div className="ySpace" />
        <div className="justifyCenter">
          <div className="gameCard maxFont">
            {this.state.enemyCard}
          </div>
        </div>
        <div className="ySpace" />
        <div className="justifyCenter flexWrap">
          <div
            className="gameCard maxFont"
            onClick={() => {
              if(!this.state.isRecord) {
                const enemyPlay = this.randomAnswer();
                let enemyIcon = '';
                let positiveScore = 0;
                let negativeScore = 0;
                switch(enemyPlay) {
                  case 1: // scissors
                    enemyIcon = '‚úÇ';
                    break;
                  case 2: // paper
                    enemyIcon = '‚ùê';
                    positiveScore = 1;
                    break;
                  case 3: // stone
                    enemyIcon = '‚åì';
                    negativeScore = 1;
                    break;
                  default:
                }
                this.setState((prevState) => {
                  const prevScore = prevState.score;
                  const prevWrongScore = prevState.wrongScore;
                  return {
                    enemyCard: enemyIcon,
                    score: prevScore + positiveScore,
                    wrongScore: prevWrongScore + negativeScore,
                    isRecord: true,
                  }
                });
              }
            }}
          >
            ‚úÇ
          </div>
          <div
            className="gameCard maxFont"
            onClick={() => {
              if(!this.state.isRecord) {
                const enemyPlay = this.randomAnswer();
                let enemyIcon = '';
                let positiveScore = 0;
                let negativeScore = 0;
                switch(enemyPlay) {
                  case 1: // scissors
                    enemyIcon = '‚úÇ';
                    negativeScore = 1;
                    break;
                  case 2: // paper
                    enemyIcon = '‚ùê';
                    break;
                  case 3: // stone
                    enemyIcon = '‚åì';
                    positiveScore = 1;
                    break;
                  default:
                }
                this.setState((prevState) => {
                  const prevScore = prevState.score;
                  const prevWrongScore = prevState.wrongScore;
                  return {
                    enemyCard: enemyIcon,
                    score: prevScore + positiveScore,
                    wrongScore: prevWrongScore + negativeScore,
                    isRecord: true,
                  }
                });
              }
            }}
          >
            ‚ùê
          </div>
          <div
            className="gameCard maxFont"
            onClick={() => {
              if(!this.state.isRecord) {
                const enemyPlay = this.randomAnswer();
                let enemyIcon = '';
                let positiveScore = 0;
                let negativeScore = 0;
                switch(enemyPlay) {
                  case 1: // scissors
                    enemyIcon = '‚úÇ';
                    positiveScore = 1;
                    break;
                  case 2: // paper
                    enemyIcon = '‚ùê';
                    negativeScore = 1;
                    break;
                  case 3: // stone
                    enemyIcon = '‚åì';
                    break;
                  default:
                }
                this.setState((prevState) => {
                  const prevScore = prevState.score;
                  const prevWrongScore = prevState.wrongScore;
                  return {
                    enemyCard: enemyIcon,
                    score: prevScore + positiveScore,
                    wrongScore: prevWrongScore + negativeScore,
                    isRecord: true,
                  }
                });
              }
            }}
          >
            ‚åì
          </div>
        </div>
        <div className="ySpace" />
        <div className="justifyCenter">
          <div
            className="next"
            onClick={() => {
              this.setState({
                isRecord: false,
                enemyCard: '',
              })
            }}
          >
            Play Again
          </div>
        </div>
        <div className="ySpace" />
      </div>
    );
  }
}

export default BasicScissorsPaperStoneGame;
